            </div>
          </div>
        </div>
        <v-btn
          color="#8AB26D"
          style="margin-top: 2%"
          class="dashboard_buttons cursor-pointer"
          @click="createForm('Catalogue')"
          >{{ $t("newUi.adminPanel.createButton") }}</v-btn
        >
      </div>
      <v-container
        v-if="payload.filter((o) => o.type != 'Ordering form').length != 0"
      >
        <div class="text_color order-form">
          {{ $t("newUi.sideBarData.catForm") }}
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
