                class="image_size cursor-pointer"
                @click="viewForm(item.link.split('/')[3])"
              ></v-img>
              <div class="mobileview shopName">{{ item.shopname }}</div>
              <div class="desktopView pt-2">
                <div class="d-flex justify-space-between">
                  <div
                    class="justify-start text-truncate shopName"
                    style="width: 110px"
                  >
                    {{ item.shopname }}
                  </div>
                  <span style="width:70px">
                    <img
