            {{ $t("newUi.editForm.deleteForm") }}
          </div>
          <div class="cancel-delete">
            <span class="cursor-pointer" @click="doCancelDelete">x</span>
          </div>
          <v-container style="position: relative;top: -15px;">
            <div>{{ $t("newUi.deleteForm.confirmDelete") }}</div>
            <v-spacer style="padding: 10px"></v-spacer>
            <v-btn
              color="#8AB26D"
              class="button_padding cursor-pointer"
              @click="doCancelDelete"
              >{{ $t("common.cancelButton") }}</v-btn
            >
            <v-btn
              color="#8AB26D"
              class="button_padding cursor-pointer"
