    <v-dialog v-model="share_dialog" content-class="crop-dialog">
      <v-container style="height: 100px; background-color: #fff">
        <div class="d-flex justify-space-between text_color">
          <div class="d-flex justyfy-start">
            {{ $t("newUi.addCategory.shareLink") }}
          </div>
          <div
            class="d-flex justify-end cursor-pointer"
            style="font-size: 14px"
          >
            <span class="cursor-pointer" @click="share_dialog = false">x</span>
          </div>
        </div>
        <div class="d-flex mt-3">
          <social-share
            :share_url="this.form_url"
            :shopname="shareInfo.shopname"
            :desc="shareInfo.desc"
          ></social-share>
          <div class="do_copy" @click="doCopy">
            <img
              class="do_copy_icon cursor-pointer"
              src="../assets/ui/copy.svg"
              alt=""
            />
          </div>
