<script>
import i18n from "../i18n";
import router from "../router/router";
import { deleteFile, loadFile, setLocale } from "../services/firebase";
import { deleteMasterData } from "../services/firebase";
import store from "../store/store";
import { saveFile } from "../services/firebase";
import socialShare from "./socialShare.vue";
export default {
  components: { socialShare },
  name: "GsItemCard",
  props: ["payload"],
  mounted() {
    console.log("payload", this.$store.state.forms);
    setLocale();
  },
  methods: {
    open: function() {
      router.push(this.payload.link);
    },
    deleteForm: function() {
      this.dialog = true;
    },
    deleteFormMobile: function(payloadlink) {
      this.dialog = true;
      this.link = payloadlink;
    },
    deleteFormDesktop: function(payloadlink) {
      this.dialogDesktop = true;
      this.link = payloadlink;
    },
    doDeleteForm: function() {
      this.dialog = false;
      this.deleteFormFile(this.payload.link);
    },
    doDeleteFormMobile: function(link) {
      this.dialog = false;
      this.dialogDesktop = false;
      this.deleteFormFile(link);
    },
    doCancelDelete: function() {
      this.dialog = false;
      this.dialogDesktop = false;
    },
    deleteFormFile: function(formlink) {
      //controller.pause();
      var storedata = this.$store;
      this.$store.dispatch("setOverlay", true);
      this.set_overlay = true;
      var elethis = this;
      for (var i = 0; i < this.$store.state.forms.length; i++) {
        console.log("formlink", formlink, this.$store.state.forms[i].link);
        if (this.$store.state.forms[i].link === formlink) {
          this.$store.state.forms.splice(i, 1);
          deleteMasterData(function(err) {
            console.log(err);
            return 0;
          }, this.$store.state);
          //var pos = parseInt(formlink.split("/")[3]);
          var pos;
          if (formlink.split("/")[3]) {
            pos = parseInt(formlink.split("/")[3]);
          } else {
            pos = parseInt(formlink.split("/")[2]);
          }
          deleteFile(this.$store.state.profile.uid, pos, function() {
            storedata.dispatch("setOverlay", false);
            elethis.set_overlay = false;
          });
          break;
        }
      }
    },
    viewForm: function(pos) {
      router.push("/admin/viewform/" + pos);
    },
    createForm(type) {
      var createPos = this.getFreePos();
      if (createPos === -1) {
        alert(i18n.t("alertText.fiveForms"));
        return;
      }
      //this.selectType(createPos);

      var newlink = "/admin/editform/" + createPos;
      var newForm = {
        id: store.state.profile.uid,
        pos: createPos,
        categs: ["Ordering form", "Catalogue"],
        shopname: "",
        shopdesc: "",
        type: type,
        catalogue: [],
        phone: "",
        amount: 0,
        maxlength: 0,
      };

      saveFile(
        store.state.profile.uid,
        createPos,
        JSON.stringify(newForm),
        function() {
          //state.forms.push({"shopname": "", "type": + type, "link":"/admin/" + createPos});
          if (router.currentRoute.fullPath != newlink) {
            router.push("/admin/editform/" + createPos);
          }
        }
      );
    },
    getFreePos() {
      var isPresent = [];
      for (var ii = 0; ii < 5; ii++) isPresent[ii] = false;
      for (var i = 0; i < this.$store.state.forms.length; i++) {
        var formlink = this.$store.state.forms[i].link;
        //var ipos = parseInt(formlink.split("/")[3]);
        var ipos;
        if (formlink.split("/")[3]) {
          ipos = parseInt(formlink.split("/")[3]);
        } else {
          ipos = parseInt(formlink.split("/")[2]);
        }
        if (ipos >= 0 && ipos < 5) isPresent[ipos] = true;
      }
      for (var j = 0; j < 5; j++) if (!isPresent[j]) return j;
      return -1;
    },
    selectType(callback) {
      this.$store.dispatch("setCurCallback", callback);
      //router.push("/newform");
      //this.$store.state.newformroute.newformdialog = true;
      store.dispatch("setOverlay", false);
    },
    openShareDialog(item) {
      this.fetchData(item.link.split("/")[3]);
      this.shareInfo = item;
      this.shareInfo.desc = item.desc ? item.desc : item.shopdesc;
      this.create_link(this.$store.state);
      this.share_dialog = true;
    },
    doCopyOutside: function(formLink) {
      this.fetchData(formLink.split("/")[3]);
      this.create_link(this.$store.state);
      var text = this.form_url;
      navigator.clipboard
        .writeText(text)
        .then(() => {
          this.$store.state.snackbartext = i18n.t("common.copy");
          this.$store.state.snackbar = true;
        })
        .catch((error) => {
          console.log(error);
        });
      /*var textField = document.createElement("textarea");
      textField.innerText = text;
      document.body.appendChild(textField);
      textField.select();
      textField.focus(); //SET FOCUS on the TEXTFIELD
      try {
        var successful = document.execCommand("copy");
        if (successful) {
          textField.remove();
          this.$store.state.snackbartext = i18n.t("common.copy");
          this.$store.state.snackbar = true;
        }
      } catch (err) {
        console.log(err);
      }*/
    },
    doCopy: function() {
      var text = this.form_url;
      if (navigator || navigator != undefined) {
        navigator.clipboard
          .writeText(text)
          .then(() => {
            this.$store.state.snackbartext = i18n.t("common.copy");
            this.$store.state.snackbar = true;
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        var textField = document.createElement("textarea");
        textField.innerText = text;
        document.body.appendChild(textField);
        textField.select();
        textField.focus(); //SET FOCUS on the TEXTFIELD

        try {
          var successful = document.execCommand("copy");
          console.log(successful);
          if (successful) {
            textField.remove();
            this.$store.state.snackbartext = i18n.t("common.copy");
            this.$store.state.snackbar = true;
          }
        } catch (err) {
          console.log(err);
        }
      }
      // var textField = document.createElement("textarea");
      // textField.innerText = text;
      // document.body.appendChild(textField);
      // textField.select();
      // textField.focus();  //SET FOCUS on the TEXTFIELD

      // try {
      //   var successful = document.execCommand("copy");
      //   console.log(successful);
      //   if (successful) {
      //     textField.remove();
      //     this.$store.state.snackbartext = i18n.t("common.copy");
      //     this.$store.state.snackbar = true;
      //   }
      // } catch (err) {
      //   console.log(err);
      // }
    },
    create_link: function(stateData) {
      if (stateData) {
        var url = window.location.protocol + "//" + location.host;
        this.form_url =
          url +
          "/form/" +
          stateData.currentForm.id +
          "/" +
          stateData.currentFormPos +
          "?lang=" +
          this.$store.state.locale;
      } else {
        this.form_url = "";
      }
    },
    fetchData(pos) {
      this.$store.state.currentFormPos = pos;
      loadFile(
        this.$store.state.profile.uid,
        pos,
        this.$store.state,
        function() {
          // if (error) {
          //   //TODO skeleton data
          //   store.dispatch("setCurrentFormData", {
          //     id: store.state.profile.uid,
          //     pos: store.state.currentFormPos,
          //     categs: ["Ordering form", "Catalogue"],
          //     shopname: "",
          //     shopdesc: "",
          //     type: "Ordering form",
          //     catalogue: [],
          //     phone: "+919000000000",
          //   });
          //   store.dispatch("setOverlay", false);
          // } else {
          //   store.dispatch("setOverlay", false);
          // }
        }
      );
    },
  },
  data: function() {
    return {
      dialog: false,
      dialogDesktop: false,
      link: "",
      form_url: "",
      share_dialog: false,
      shareInfo: {},
      set_overlay: false,
      order_info: [
        i18n.t("newUi.sideBarData.dbDesc1"),
        i18n.t("newUi.sideBarData.dbDesc2"),
      ],
      catalogue_info: [
        i18n.t("newUi.sideBarData.dbDesc4"),
        i18n.t("newUi.sideBarData.dbDesc3"),
      ],
      // order_images: ["@/assets/home-image1-1.png",
      //                       "@/assets/home-image1-1.png"]
    };
  },
  computed: {
    bkgcolor: function() {
      // var pos = this.payload.link.split("/")[3];

      var pos;
      if (this.payload.link.split("/")[3]) {
        pos = parseInt(this.payload.link.split("/")[3]);
      } else {
        pos = parseInt(this.payload.link.split("/")[2]);
      }

      var p = parseInt(pos + "") % 5;
      if (p == 0) return "red";
      if (p == 1) return "blue";
      if (p == 2) return "green";
      if (p == 3) return "brown";
      return "black";
    },
    type: function() {
      if (this.payload.type == "Ordering form") {
        return i18n.t("common.typeOrderForm");
      } else {
        return i18n.t("common.typeCatelogue");
      }
    },
  },
};
</script>
